var ABC, AbstractCell, Collision, Item, Snake, Void, Wall, _, covenance, errors, ref;

_ = require('underscore');

errors = require('../../utility').errors;

ref = require('covenance'), covenance = ref.covenance, ABC = ref.ABC;

AbstractCell = ABC({
  name: 'AbstractCell',
  klass: {
    covenance: covenance.of(['shortname', _.isString])
  },
  proto: {
    props: {
      toString: function() {
        return this.constructor.shortname;
      }
    }
  }
});

Item = AbstractCell.implementation(Item = (function() {
  function Item() {}

  Item.shortname = "item";

  return Item;

})());

Void = AbstractCell.implementation(Void = (function() {
  function Void() {}

  Void.shortname = "void";

  return Void;

})());

Wall = AbstractCell.implementation(Wall = (function() {
  function Wall() {}

  Wall.shortname = "wall";

  return Wall;

})());

Snake = AbstractCell.implementation(Snake = (function() {
  function Snake() {}

  Snake.shortname = "snake";

  return Snake;

})());

Collision = AbstractCell.implementation(Collision = (function() {
  function Collision() {}

  Collision.shortname = "collision";

  return Collision;

})());

module.exports = {
  ITEM: new Item,
  WALL: new Wall,
  VOID: new Void,
  SNAKE: new Snake,
  COLLISION: new Collision
};
