var ABC, AbstractAction, Immutable, _, covenance, errors, ref;

_ = require('underscore');

errors = require('../utility').errors;

Immutable = require('immutable');

ref = require('covenance'), covenance = ref.covenance, ABC = ref.ABC;

AbstractAction = ABC({
  name: 'AbstractAction',
  klass: {
    covenance: covenance.of({
      attribute: 'validate_payload',
      validator: _.isFunction
    }),
    props: {
      value_of: function(payload) {
        this.validate_payload(payload);
        return this.post_value_of_hook(payload);
      },
      post_value_of_hook: function(payload) {
        var instance;
        instance = new this;
        instance.payload = payload;
        return instance;
      }
    }
  },
  proto: {
    props: {
      toString: function() {
        return this.constructor.name;
      },
      is_a: function(klass) {
        return this instanceof klass;
      }
    }
  }
});

module.exports = AbstractAction;
