var AbstractAction, Immutable, KeyDownAction, XY, _;

AbstractAction = require('./action');

_ = require('underscore');

Immutable = require('immutable');

XY = require('../utility').XY;

KeyDownAction = AbstractAction.implementation(KeyDownAction = (function() {
  function KeyDownAction() {}

  KeyDownAction.KEYCODES = Immutable.Set([37, 38, 39, 40, 82]);

  KeyDownAction.METHOD_KEYS = Immutable.Map([[82, 'restart']]);

  KeyDownAction.MOTION_KEYS = Immutable.Map([[37, XY.left()], [38, XY.up()], [39, XY.right()], [40, XY.down()]]);

  KeyDownAction.validate_payload = function(payload) {
    if (!(_.isObject(payload) && _.isNumber(payload.keycode))) {
      throw new Error('Expected payload to have a number keycode property');
    }
  };

  KeyDownAction.post_value_of_hook = function(payload) {
    var keycode;
    keycode = payload.keycode;
    if (this.KEYCODES.has(keycode)) {
      return Object.getPrototypeOf(this).post_value_of_hook.call(this, payload);
    } else {
      throw new Error("Unsupported keycode: " + keycode + "; try " + this.KEYCODES);
    }
  };

  KeyDownAction.prototype.keycode = function() {
    return this.payload.keycode;
  };

  KeyDownAction.prototype.is_motion = function() {
    return this.constructor.MOTION_KEYS.has(this.keycode());
  };

  KeyDownAction.prototype.is_method = function() {
    return this.constructor.METHOD_KEYS.has(this.keycode());
  };

  KeyDownAction.prototype.motion = function() {
    return this.constructor.MOTION_KEYS.get(this.keycode());
  };

  KeyDownAction.prototype.method = function() {
    return this.constructor.METHOD_KEYS.get(this.keycode());
  };

  return KeyDownAction;

})());

module.exports = {
  KeyDownAction: KeyDownAction
};
