var _,
  slice = [].slice;

_ = require('underscore');

module.exports = {
  is_nonempty_string: function(thing) {
    return _.isString(thing) && (thing.length > 0);
  },
  is_obj_literal: function(thing) {
    return _.isObject(thing) && !Array.isArray(thing) && !_.isFunction(thing);
  },
  enable_mixing: function(thing, mixer, mixing_prop, mixing_aliases) {
    var alias, i, len;
    if (mixing_aliases == null) {
      mixing_aliases = null;
    }
    if (thing[mixing_prop] != null) {
      return false;
    } else {
      Object.defineProperty(thing, mixing_prop, {
        enumerable: false,
        value: mixer
      });
      if (mixing_aliases != null) {
        for (i = 0, len = mixing_aliases.length; i < len; i++) {
          alias = mixing_aliases[i];
          Object.defineProperty(thing, alias, {
            enumerable: false,
            value: function() {
              var args;
              args = 1 <= arguments.length ? slice.call(arguments, 0) : [];
              return this[mixing_prop].apply(this, args);
            }
          });
        }
      }
      return true;
    }
  }
};
