var ABC, AbstractKey, Down, Left, R, Right, Up, _, covenance, errors, ref;

_ = require('underscore');

errors = require('../../utility').errors;

ref = require('covenance'), covenance = ref.covenance, ABC = ref.ABC;

AbstractKey = ABC({
  name: 'AbstractKey',
  proto: {
    props: {
      symbol: function() {
        return this.constructor.symbol;
      },
      keycode: function() {
        return this.constructor.keycode;
      },
      shortname: function() {
        return this.constructor.shortname;
      }
    }
  },
  klass: {
    covenance: covenance.of(['symbol', _.isString], ['keycode', _.isNumber], ['shortname', _.isString])
  }
});

Left = AbstractKey.implementation(Left = (function() {
  function Left() {}

  Left.symbol = "←";

  Left.keycode = 37;

  Left.shortname = "left";

  return Left;

})());

Up = AbstractKey.implementation(Up = (function() {
  function Up() {}

  Up.symbol = "↑";

  Up.keycode = 38;

  Up.shortname = "up";

  return Up;

})());

Right = AbstractKey.implementation(Right = (function() {
  function Right() {}

  Right.symbol = "→";

  Right.keycode = 39;

  Right.shortname = "right";

  return Right;

})());

Down = AbstractKey.implementation(Down = (function() {
  function Down() {}

  Down.symbol = "↓";

  Down.keycode = 40;

  Down.shortname = "down";

  return Down;

})());

R = AbstractKey.implementation(R = (function() {
  function R() {}

  R.symbol = "R";

  R.keycode = 82;

  R.shortname = "r";

  return R;

})());

module.exports = {
  LEFT: new Left,
  UP: new Up,
  RIGHT: new Right,
  DOWN: new Down,
  R: new R
};
